@import './variables.css';

.landing-page {
    position: relative;
    display: flex;
    height: 100vh;
    width: 100%;
    justify-content: center;
    align-items: center;
}
.outline-thick {
    -webkit-text-stroke-width: 3px;
}
.name-border {
    position: absolute;
    left: 0;
    width: 100%;
}
.name-bar {
    position: absolute;
    margin: 0;
    padding: 0;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 9vw;
}
.tron-theme .name-bar {
    padding: 26px 60px;
    border-radius: 50px;
    border: 20px solid var(--tron-green);
}
.color-theme .name-bar {
    height: 100%;
    width: 100%;
    color: var(--lime);
}
@media screen and (max-width: 992px) {
    .color-theme .landing-page {
        height: 100vh;
    }
    .bordered {
        -webkit-text-stroke-width: 2px;
    }
    .name-border {
        width: 100%;
        margin-left: 0;
    }
    .tron-theme .name-bar {
        padding: 0;
        height: 194px;
        width: 90%;
        font-size: 11vw;
        border: 12px solid var(--tron-green);
        border-radius: 30px;
    }
    .color-theme .name-bar {
        font-size: 12vw;
    }
}

@media screen and (max-width: 600px) {
    .tron-theme .name-bar {
        font-size: 16vw;
    }
}